.pdfWrapper {
    width: 100%;
    /* height: calc(100vh - 160px); */
    height: 100%;
    margin: auto;
    overflow-y: auto;
    overflow-x: hidden;
}

.pdfNavbar {
    margin: 0 auto;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    padding: 10px 15px;
}

.pdfNav {
    color: red;
}

.pdfnavDisabled {
    color: grey;
}

.pageWrapper {
    width: 100%;
}

.pageWrapper canvas {
    width: 100%;
    margin: auto;
    max-width: 100%;
    height: auto !important;
}

.pdfContentWrapper {
    max-height: 70vh;
}