.hotspot {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    user-select: none;
    cursor: pointer;
}

.hotspotImg {
    display: flex;
}

.hotspoterror {
    color: red;
}

.hotspotImg img {
    width: 55px;
}

.hotspotTextContainer {
    width: max-content;
    left: 50%;
    position: absolute;
    transform-origin: center top;
    z-index: -1;
}

.hotspotDisplayName{
    width: 0px;
    display: block;
    box-sizing: border-box;
    background: white;
    padding: 0.42rem;
    padding-left: 1.7rem;
    padding-right: 1rem;
    border-radius: 0 30px 30px 0;
    color: transparent;
    visibility: hidden;
    white-space: noWrap;
    text-align: center;
    font-size: 0.8rem;
    font-weight: 600;
}

.hotspot:hover .hotspotDisplayName {
    transition: color 0.3s 0.2s ease-out, width 0.3s ease-out, visibility 0.3s ease-out;
    visibility: visible;
    width: 100%;
    color: #000;
}



@media only screen and (min-device-width: 320px) and (max-device-width: 900px) {
    .hotspotImg img {
        width: 40px;
    }
    .hotspotDisplayName {
        font-size: 10px;
        padding: 0.25rem .5rem;
    }
}


/* Iframe Styles */
.iframeWrapper {
    width: 100%;
    /* height: calc(100vh - 200px); */
    height: 80vh;
}
.iframeWrapper iframe {
    border: none;
    width: 100%;
    height: 100%;
}
/* Iframe Styles */