.offCanvasModalContainer {
    position: fixed;
    top: 0;
    right: 0;
    width: 40%;
    height: 100vh;
    background: #fff;
    overflow: auto;
    z-index: 15;
    padding: 1rem;
    animation: slideFromRight 0.1s ease-out;
}

@keyframes slideFromRight {
    0% {
        transform: translateX(50%);
    }
    100% {
        transform: translateX(0);
    }
}

.offCanvasModalBackdrop {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.6);
    overflow: auto;
    z-index: 14;
}

.modalHeader {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 1rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.modalCloseBtn {
    cursor: pointer;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
}

.modalFooter {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1rem;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.modalBody {
    height: calc(100vh - 150px);
    overflow: auto;
    padding: 1rem;
    padding-bottom: 3rem;
}