.formGroup {
    margin: 5px;
    margin-bottom: 10px;
}

.label {
    display: block;
    font-weight: 600;
    /* font-weight: bold; */
    font-size: 0.9rem;
}

.input {
    border: 1px solid #333;
    border-radius: 8px;
    padding: 5px 8px;
    display: block;
    width: 100%;
}

.input[type=checkbox] {
    width: 25px;
    height: 25px;
}

.textarea {
    border: 1px solid #333;
    border-radius: 8px;
    padding: 5px 8px;
    display: block;
    width: 100%;
    height: 100px;
}

.select {
    border: 1px solid #333;
    border-radius: 8px;
    padding: 5px 8px;
    display: block;
    width: 100%;
    text-transform: capitalize;
}

.select option {
    text-transform: capitalize;
}

.dark input,
.dark textarea,
.dark select {
    background-color: #333;
    color: #fff;
}

.error {
    color: #dc3545;
}