.userMenu {
    display: flex;
}

.userMenuItem {
    margin: 0 0.5rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.langSelectorBtn {
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    box-shadow: none !important;
}

.langSelectorBtn svg {
    font-size: 1.5rem;
}

.forceRTL.userMenu {
    flex-direction: row-reverse;
}