.appHeader {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    padding: 0.5rem 1rem;
    background: #fff;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: space-between;
    user-select: none;
}

.appHeader.forceRTL {
    flex-direction: row-reverse;
}