.welcomeVideoWrapper {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #121212;
    z-index: 10;
}

.skipBtn {
    position: absolute;
    right: 0;
    bottom: 0;
    margin: 1rem;
    z-index: 1;
    text-transform: uppercase;
    background: rgba(0, 0, 0, 0.1);
}

.welcomeVideo {
    object-fit: cover;
    width: 100%;
    height: 100%;
}